.yo-scroll {
  position: relative;
  overflow: auto;
  z-index: 100;
  width: 100%;
  height: auto;
  -webkit-overflow-scrolling: touch;
}
.yo-scroll .inner {
  position: relative;
  width: 100%;
  height: auto;
  transition-duration: 300ms;
}
.yo-scroll .inner .pull-refresh {
  position: absolute;
  background: #fff;
  left: 0;
  top: -1rem;
  width: 100%;
  height: 1rem;
  display: flex;
  display: -webkit-flex;
  align-items: center;
  justify-content: center;
}
.yo-scroll .inner .pull-refresh:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 5rem;
  background: #fff;
  left: 0;
  bottom: 100%;
}
.yo-scroll .inner .load-more {
  height: 1rem;
  line-height: 1rem;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  display: none;
}
.yo-scroll .inner .down-tip,
.yo-scroll .inner .refresh-tip,
.yo-scroll .inner .up-tip {
  display: none;
}
.yo-scroll .inner .up-tip:before,
.yo-scroll .inner .refresh-tip:before {
  content: '';
  display: inline-block;
  width: 160px;
  height: 70px;
  background-size: 70% !important;
  position: absolute;
  top: 0;
  left: 20%;
}
.yo-scroll.touch .inner {
  transition-duration: 0;
}
.yo-scroll.down .down-tip {
  display: block;
}
.yo-scroll.up .up-tip {
  display: block;
}
.yo-scroll.refresh .refresh-tip {
  display: block;
}
